.App {
    font-family: 'Montserrat', 'Roboto', sans-serif;
}

.section {
    padding-bottom: 35px;
}

.hwTitle {
    font-size: 22px;
    font-weight: 600;
    line-height: 1.3;
    padding-bottom: 10px;
}

.container {
    max-width: 1170px;
    padding: 0 15px;
    width: 100%;
    margin: 0 auto;
}

.wrapper {
    position: relative;
}

.wrapper::before,
.wrapper::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    left: 0;
    background-color: #d9d9d9;
}

.wrapper::before {
    top: 0;
}

.wrapper::after {
    bottom: 0;
}

.hw {
    padding: 24px 15px 32px;
}

.header {
    background-color: #ffffff;
    box-shadow: 0 0 40px rgba(29, 33, 38, 0.13), 0 0 2px rgba(29, 33, 38, 0.1);
    margin-bottom: 47px;
    padding: 14px 0;
}

.headerWrapper {
    display: flex;
    justify-content: space-between;
}