.sendForm {
    display: flex;
    justify-content: space-between;
    padding-top: 60px;
}

.textarea {
    max-width: 976px;
    width: 100%;
    border-radius: 20px;
    margin-right: 20px;
    resize: none;
    min-height: 40px;
    border: 0;
    padding: 12px 24px;
    background-color: #f5f7fb;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
}

.button {
    padding: 10px 40px;
    background-color:  #0066cc;
    border-radius: 20px;
    transition: opacity 0.4s;
    color: #ffffff;
    font-size: 16px;
    font-weight: 600;
    border: 0;
    text-align: center;
}

.button:hover {
    opacity: 0.8;
}

.button:active {
    opacity: 0.5;
}
