.friendMessage {

}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
}

.avatar {
    max-width: 48px;
    width: 100%;
    height: 48px;
    object-fit: cover;
    border-radius: 50%;
    margin-bottom: 5px;
}

.friendText {
    background-color: #f5f7fb;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 8px;
    padding: 7px 13px;
    margin-right: 10px;
    position: relative;
}

.friendText:after {
    content: '';
    position: absolute;
    left: -9px;
    transform: rotate(-178deg);
    bottom: 0;
    border: 10px solid transparent;
    border-top: 10px solid #f5f7fb;
}

.friendName {
    margin-bottom: 4px;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.3;
    display: block;
}

.friendMessageText {
    font-size: 16px;
}

.friendTime {
    font-size: 10px;
    font-weight: 600;
    color: #000000;
    padding-left: 10px;
}
