.message {
    margin-left: auto;
    margin-bottom: 30px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
}

.avatar {
    max-width: 48px;
    width: 100%;
    height: 48px;
    object-fit: cover;
    border-radius: 50%;
    margin-bottom: 5px;
}

.text {
    background-color: #0066cc;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 8px;
    padding: 7px 13px;
    color: #ffffff;
    margin-right: 10px;
    position: relative;
}

.text:after {
    content: '';
    position: absolute;
    right: -9px;
    transform: rotate(-178deg);
    bottom: 0;
    border: 10px solid transparent;
    border-top: 10px solid #0066cc;
}

.name {
    margin-bottom: 4px;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.3;
    display: block;
    text-align: right;
}

.messageText {
    font-size: 16px;
}

.time {
    font-size: 10px;
    font-weight: 600;
    color: #000000;
    text-align: right;
    padding-right: 12px;
}