.sidebar {
    display: none;
    position: absolute;
    background-color: #ffffff;
    top: 0;
    bottom: 0;
    left: 0;
    height: 100vh;
    overflow-y: auto;
    padding: 16px 24px 16px 70px;
    z-index: 10;
    max-width: 317px;
    width: 100%;
}

.sidebar.open {
    display: block;
}

.background {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000000;
    opacity: 0.3;
    z-index: 5;
}

.nav {

}

.nav a {
    display: block;
    font-size: 22px;
    font-weight: 600;
}

.nav a:not(:last-child) {
    margin-bottom: 45px;
}

.nav a:hover {
    text-decoration: underline;
}

.nav a.active {
    text-decoration: underline;
}

.close {
    cursor: pointer;
    border: 0;
    background-color: transparent;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: auto;
}

.close img {
    width: 12px;
    height: 12px;
    object-fit: contain;
}
